.App {
  background-color: black;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* > * {
    margin: 0;
}

body{
  /*  fallback for old browsers */
   background: #F2F2F2;
  /* background: -webkit-linear-gradient(to right, #24243e, #302b63, #0f0c29);  
  background: linear-gradient(to right, #24243e, #302b63, #0f0c29); 
   */
}

.app-one {
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 20px;
}

/* Large rounded green border */
.border {
  border: 3px solid #0f0c29;
  border-radius: 5px;
  margin-bottom: 20px;
}

h4{
  margin-top: 50px;
}

.register{
  max-width: 978px;
  width: 100vw;
  display:flex;
  border: 1px solid #e9ecef;
  border-radius: 5px;
  /* box-shadow: 1px 3px 10px #e9ecef; */
  background-color: white;
}

.register span{
  color: #adb5bd;
}

#form{
  max-width: 320px;
  width: 100vw;
  margin: 2em auto;
}

#form >  input, .btn{
  border: 1px solid #e9ecef;
  padding: .9em 1em;
}

input{
  
    border: 1px solid #e9ecef;
    padding: .9em 1em;
    
}

#form > input:focus{
  outline: none;
}

#form > .btn{
  background-color: #003049;
  color: #e9ecef;
  font-size: 1em;
  cursor: pointer;
}

.flex{
  display: flex;
  gap: 1em;
}

.flex-col{
  flex-direction: column;
}

.register .col-1{
  margin: auto;
  padding: 3em 0;
}

.register .col-2 img{
  width: 480px;
  height: 100%;
  object-fit: cover;
  align-self: flex-end;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  display: none;
}

.dynamic-input{
   width: 80%;
   border: 1px solid #0a0a0a;
   padding: .9em 1em;
   margin: 1em auto;
}

.btn-remove{
  background-color: #e71616;
  border: 1px solid #e9ecef;
  padding: .9em 1em;
  color: #e9ecef;
  font-size: 1em;
  cursor: pointer;
}

@media screen and (min-width: 860px)  {
  .register .col-2 img{
    display: initial;
  }
}

